


























import { NavBar, HomeSider, MenuTab } from "@/components";
import { Component, Vue } from "vue-property-decorator";
@Component({
  components: {
    NavBar,
    HomeSider,
    MenuTab
  }
})
export default class Index extends Vue {
  isCollapse: boolean = false;
  changeSiderStatus() {
    this.isCollapse = !this.isCollapse;
  }
}
